
















import Vue from "vue";

export default Vue.extend({
  name: "search",
  data() {
    return {
      searchQuery: "" as string, // поисковый запрос
    };
  },
  methods: {
    findPhones(e?: KeyboardEvent) {
      // поиск номеров телефонов
      if (e) {
        if (e.key === "Enter") {
          this.$store.dispatch("setSearchQueryAction", this.searchQuery);
        }
      } else {
        this.$store.dispatch("setSearchQueryAction", this.searchQuery);
      }
    },
    clearSearch() {
      // очистка поиска
      this.searchQuery = "";
      this.$store.dispatch("setSearchQueryAction", this.searchQuery);
    }
  },
});
